import React from "react";
import { Row, Column } from "components/Grid";
import AlternatingSequence from "components/AlternatingSequence";

import {
  Wrapper,
  Content,
  BulletPointWrapper,
  Bullet,
  Header,
  TransparentButton,
  ButtonWrappers,
  Gutter,
  LinksWrapper,
  StyledLink,
  DetailsSection,
} from "./elements";
import { valuePropsMessages } from "../messages";

interface BulletPointProps {
  children: string;
}

export const BulletPoint = ({ children }: BulletPointProps) => (
  <BulletPointWrapper>
    <Bullet />
    <Content>{children}</Content>
  </BulletPointWrapper>
);

const formattedMessages = {
  ...valuePropsMessages,
  sections: valuePropsMessages.sections.map((section) => ({
    ...section,
    description: (
      <React.Fragment>
        {section.description.map((descriptionBullet, index) => (
          <BulletPoint key={`bullet-${index + 1}`}>
            {descriptionBullet}
          </BulletPoint>
        ))}
      </React.Fragment>
    ),
  })),
};

const ValueProps = () => (
  <Wrapper>
    <AlternatingSequence messages={formattedMessages} />
    <LinksWrapper>
      <DetailsSection>
        <Row
          padding={30}
          gutterWidth={26}
          verticalAlignment="flex-start"
          horizontalAlignment="space-between"
        >
          <Column span={0} spanMedium={1} spanSmall={0} />
          <Column span={5} spanMedium={10} spanSmall={12}>
            <Header>Learn more about Brex&apos;s current offerings</Header>
          </Column>
          <Column span={0} spanMedium={1} spanSmall={0} />
          <Column span={0} spanMedium={1} spanSmall={0} />
          <Column span={6} spanMedium={10} spanSmall={12}>
            <ButtonWrappers>
              <StyledLink to="/startups">
                <TransparentButton>BREX FOR STARTUPS</TransparentButton>
              </StyledLink>
              <Gutter />
              <StyledLink to="/ecommerce">
                <TransparentButton>BREX FOR ECOMMERCE</TransparentButton>
              </StyledLink>
            </ButtonWrappers>
          </Column>
          <Column span={0} spanMedium={1} spanSmall={0} />
        </Row>
      </DetailsSection>
    </LinksWrapper>
  </Wrapper>
);

export default ValueProps;
