import React from "react";
import { withStateHandlers } from "recompose";

import arrowUpWhite from "assets/img/icons/white/arrow-up.svg";
import arrowUpOrange from "assets/img/icons/orange/arrow-up.svg";

import {
  BlocksContainer,
  BlogBlock,
  StyledLink,
  IconContainer,
  Icon,
  Black,
  SimpleButton,
  Wrapper,
} from "./elements";

const blogPosts = [
  {
    title: "How to set up accounting for your startup",
    link: "/blog/accounting",
  },
  {
    title: "Accounting, according to founders",
    link: "/blog/scaling/when-should-you-invest-in-startup-accounting",
  },
  {
    title: "Don't fear the audit: A big 4 auditor discusses how to prepare",
    link: "/blog/scaling/former-big-four-auditor-advice-for-audits-startups",
  },
];

const BlogLinks = ({ activeIndex, setActiveIndex, unsetActiveIndex }) => (
  <Wrapper>
    <BlocksContainer>
      {blogPosts.map((post, index) => (
        <StyledLink
          to={post.link}
          key={`blog-post-${index + 1}`}
          onMouseEnter={() => setActiveIndex(index)}
          onMouseLeave={unsetActiveIndex}
        >
          <BlogBlock>
            <Black>{post.title}</Black>
            <IconContainer>
              <Icon
                src={activeIndex === index ? arrowUpWhite : arrowUpOrange}
              />
            </IconContainer>
          </BlogBlock>
        </StyledLink>
      ))}
    </BlocksContainer>
    <SimpleButton>
      <StyledLink to="/blog">VIEW MORE</StyledLink>
    </SimpleButton>
  </Wrapper>
);

export default withStateHandlers(
  { activeIndex: -1 },
  {
    setActiveIndex: () => (index) => ({
      activeIndex: index,
    }),
    unsetActiveIndex: () => () => ({
      activeIndex: -1,
    }),
  },
)(BlogLinks);
