import React from "react";
import { Row, Column } from "components/Grid.tsx";

import {
  Container,
  Quote,
  TestimonialWrapper,
  UserContainer,
  User,
  Logo,
  Bar,
  Wrapper,
  CenteringWrapper,
} from "./elements";

const Testimonial = ({ testimonial, quoteHeight }) => (
  <Container>
    <TestimonialWrapper>
      <Quote quoteHeight={quoteHeight}>{testimonial.quote}</Quote>
      <UserContainer>
        <User>
          {`${testimonial.user.name},`}
          <br />
          {`${testimonial.user.role}`}
        </User>
        <Logo
          alt="testimonial"
          src={testimonial.company.logo}
          height={testimonial.company.height}
          width={testimonial.company.width}
        />
      </UserContainer>
    </TestimonialWrapper>
    <Bar />
  </Container>
);

const TestimonialSection = ({ testimonials, quoteHeight }) => (
  <Wrapper>
    <Row padding={30} gutterWidth={26}>
      {testimonials.map((testimonial) => (
        <React.Fragment key={`testimonial-${testimonial.user.name}`}>
          <Column span={6} spanMedium={12}>
            <CenteringWrapper>
              <Testimonial
                testimonial={testimonial}
                quoteHeight={quoteHeight}
              />
            </CenteringWrapper>
          </Column>
        </React.Fragment>
      ))}
    </Row>
  </Wrapper>
);

export default TestimonialSection;
